import React, { useState } from 'react'
import BigNumber from 'bignumber.js'
import styled from "styled-components";
import { Button, Flex, Heading } from '@pancakeswap-libs/uikit'
import useI18n from 'hooks/useI18n'
import { useHarvest } from 'hooks/useHarvest'
import { getBalanceNumber } from 'utils/formatBalance'
import { useWeb3React } from '@web3-react/core'
import { getAddress } from "utils/addressHelpers";
import { useStakeV2 } from "hooks/useStake";

const ButtonText = styled.div`
  :after {
    content: '(Resets Lock Period)';
    font-size: 10px;
    font-weight:300;
    display:block;
    text-align: center;
    width: 100%;
  }
  white-space: pre;
  position: relative;
`;

interface FarmCardActionsProps {
  earnings?: BigNumber
  farmAddress: string
  index: number

}

const HarvestAction: React.FC<FarmCardActionsProps> = ({ earnings, farmAddress, index }) => {
  const { account } = useWeb3React()
  const TranslateString = useI18n()
  const [pendingTx, setPendingTx] = useState(false)
  const { onStake } = useStakeV2(farmAddress, index);

  const rawEarningsBalance = account ? getBalanceNumber(earnings) : 0
  const displayBalance = rawEarningsBalance.toLocaleString()

  return (
    <Flex mb="8px" justifyContent="space-between" alignItems="center">
      <Heading color={rawEarningsBalance === 0 ? 'textDisabled' : 'text'}>{displayBalance}</Heading>
      <Button
        style={{minWidth: 200}}
        disabled={rawEarningsBalance === 0 || pendingTx}
        onClick={async () => {
          setPendingTx(true)
          await onStake('0');
          setPendingTx(false)
        }}
      >
        <ButtonText>{TranslateString(562, 'Harvest')}</ButtonText>
      </Button>
    </Flex>
  )
}

export default HarvestAction
