import React from 'react'
import { Tag, VerifiedIcon, CommunityIcon, BinanceIcon } from '@pancakeswap-libs/uikit'

const CoreTag = (props) => (
  <Tag variant="secondary" style={{color:"#fff", border: '1px solid #6BE03B'}} outline startIcon={<VerifiedIcon />} {...props}>
    Core
  </Tag>
)

const LockExpiredTag = (props) => (
  <Tag variant="secondary" style={{color:"#fff", border: '1px solid #6BE03B'}} outline startIcon={<VerifiedIcon />} {...props}>
    Soft Unlocked
  </Tag>
)

const CommunityTag = (props) => (
  <Tag variant="textSubtle" outline startIcon={<CommunityIcon />} {...props}>
    Community
  </Tag>
)

const BinanceTag = (props) => (
  <Tag variant="binance" outline startIcon={<BinanceIcon />} {...props}>
    Binance
  </Tag>
)

const DualTag = (props) => (
  <Tag variant="textSubtle" outline {...props}>
    Dual
  </Tag>
)

export { CoreTag, CommunityTag, BinanceTag, DualTag, LockExpiredTag }
