import React, { useState } from "react";
import Countdown from "react-countdown";
import styled from "styled-components";
import { Tag, Flex, Heading, Image } from "@pancakeswap-libs/uikit";
import { CommunityTag, CoreTag, LockExpiredTag } from "components/Tags";

import ibitImg from 'assets/images/farms/ibit.png';
import ibitBak from 'assets/images/farms/ibit-bak.png';
import ibitEth from 'assets/images/farms/ibit-eth.png';

export interface ExpandableSectionProps {
  lpLabel?: string;
  noPenaltyTime?: number;
  isCommunityFarm?: boolean;
  farmImage?: string;
  tokenSymbol?: string;
}

const Wrapper = styled(Flex)`
  svg {
    margin-right: 4px;
  }
`;

const MultiplierTag = styled(Tag)`
  margin-left: 4px;
`;


const CardHeading: React.FC<ExpandableSectionProps> = ({
  lpLabel,
  noPenaltyTime,
  isCommunityFarm,
  farmImage,
  tokenSymbol,
}) => {
  const [comp, setCompleted] = useState(false);
  
  const countdownTimerComplete = Number(new Date()) < noPenaltyTime;

  return (
    <Wrapper justifyContent="space-between" alignItems="center" mb="12px">
      
      {farmImage === 'ibit' && (
        <Image
          src={ibitImg}
          alt={tokenSymbol}
          width={64}
          height={64}
        />
      )}

      {farmImage === 'ibit-bak' && (
        <Image
          src={ibitBak}
          alt={tokenSymbol}
          width={64}
          height={64}
        />
      )}

      {farmImage === 'ibit-eth' && (
        <Image
          src={ibitEth}
          alt={tokenSymbol}
          width={64}
          height={64}
        />
      )}

      <Flex flexDirection="column" alignItems="flex-end">
        <Heading mb="4px">{lpLabel}</Heading>
        <Flex justifyContent="center">
          {countdownTimerComplete ? (
            <Countdown
              date={noPenaltyTime}
              onMount={({ completed }) => {
                if (completed) {
                  setCompleted(true);
                }
              }}
              onComplete={() => setCompleted(true)}
              // @ts-ignore
              renderer={renderCounter}
            />
          ) : <LockExpiredTag/>}

          {/* {noPenaltyTime}
          {isCommunityFarm ? <CommunityTag /> : <CoreTag />}
          <MultiplierTag variant="secondary">{noPenaltyTime}</MultiplierTag> */}
        </Flex>
      </Flex>
    </Wrapper>
  );
};

export default CardHeading;

const InfoText = styled.div`
color: grey;
font-size: 12px;
`

const renderCounter = (
  { days, hours, minutes, seconds, completed }: any
) => {
  if (completed) {
    return <></>;
  }
  return (
    <InfoText>
      <br/>
      Your token lock expires: {days}d {hours}h {minutes}m {seconds}s
    </InfoText>
  );
};
