import BigNumber from "bignumber.js";
import { useCallback } from "react";
import { useWeb3React } from "@web3-react/core";
import { useDispatch } from "react-redux";
import useWeb3 from "hooks/useWeb3";
import { fetchFarmUserDataAsync, fetchFarmV2UserDataAsync } from "state/actions";
import { stake, stakeV2 } from "utils/callHelpers";
import { getFarmV2Contract } from "utils/contractHelpers";
import { getAddress } from "utils/addressHelpers";

import { useMasterchef } from "./useContract";

export const useStake = (pid: number) => {
  const dispatch = useDispatch();
  const { account } = useWeb3React();
  const masterChefContract = useMasterchef();

  const handleStake = useCallback(
    async (amount: string) => {
      const txHash = await stake(masterChefContract, pid, amount, account);
      dispatch(fetchFarmUserDataAsync(account));
      console.info(txHash);
    },
    [account, dispatch, masterChefContract, pid],
  );

  return { onStake: handleStake };
};

export const useStakeV2 = (farmAddress: string, index: number) => {
  const dispatch = useDispatch();
  const { account } = useWeb3React();
  const web3 = useWeb3()
  const handleStake = useCallback(
    async (amount: string) => {
      const farmContract = getFarmV2Contract(farmAddress, web3);
      const txHash = await stakeV2(farmContract, index, Number(amount).toFixed(0), account);
      dispatch(fetchFarmV2UserDataAsync(account));
      console.info(txHash);
    },
    [account, dispatch, farmAddress, index, web3],
  );

  return { onStake: handleStake };
};

export default useStake;
