import { useCallback } from "react";
import { useWeb3React } from "@web3-react/core";
import { useDispatch } from "react-redux";
import useWeb3 from "hooks/useWeb3";
import BigNumber from "bignumber.js";
import {
  fetchFarmUserDataAsync,
  fetchFarmV2UserDataAsync,
  updateUserStakedBalance,
  updateUserBalance,
  updateUserPendingReward,
} from "state/actions";
import { unstake, unstakeV2 } from "utils/callHelpers";
import { getFarmV2Contract } from "utils/contractHelpers";
import { useMasterchef } from "./useContract";

export const useUnstake = (pid: number) => {
  const dispatch = useDispatch();
  const { account } = useWeb3React();
  const masterChefContract = useMasterchef();

  const handleUnstake = useCallback(
    async (amount: string) => {
      const txHash = await unstake(masterChefContract, pid, amount, account);
      dispatch(fetchFarmUserDataAsync(account));
      console.info(txHash);
    },
    [account, dispatch, masterChefContract, pid],
  );

  return { onUnstake: handleUnstake };
};

export const useUnstakeV2 = (farmAddress: string, index: number) => {
  const dispatch = useDispatch();
  const { account } = useWeb3React();
  const web3 = useWeb3()

  const handleUnstake = useCallback(
    async (amount: string) => {
      
      const farmContract = getFarmV2Contract(farmAddress, web3);
      const txHash = await unstakeV2(farmContract, index, amount, account);
      dispatch(fetchFarmV2UserDataAsync(account));
      console.info(txHash);
    },
    [account, dispatch, farmAddress, index, web3],
  );

  return { onUnstake: handleUnstake };
};


export default useUnstake;
