import React, { useState, useCallback } from "react";
import BigNumber from "bignumber.js";
import styled from "styled-components";
import { provider as ProviderType } from "web3-core";
import { getAddress } from "utils/addressHelpers";
import { getBep20Contract, getFarmV2Contract } from "utils/contractHelpers";
import { Button, Flex, Text } from "@pancakeswap-libs/uikit";
import { FarmV2 } from "state/types";
import { useFarmV2FromSymbol, useFarmV2User } from "state/hooks";
import useI18n from "hooks/useI18n";
import useWeb3 from "hooks/useWeb3";
import { useApprove } from "hooks/useApprove";
import ConnectWalletButton from "components/ConnectWalletButton";
import StakeAction from "./StakeAction";
import HarvestAction from "./HarvestAction";

const Action = styled.div`
  padding-top: 16px;
`;
export interface FarmV2WithStakedValue extends FarmV2 {
  apy?: BigNumber;
}

interface FarmV2CardActionsProps {
  farm: FarmV2WithStakedValue;
  provider?: ProviderType;
  account?: string;
  addLiquidityUrl?: string;
}

const CardActions: React.FC<FarmV2CardActionsProps> = ({
  farm,
  account,
  addLiquidityUrl,
}) => {
  const TranslateString = useI18n();
  const [requestedApproval, setRequestedApproval] = useState(false);
  const {
    farm: { lpAddresses },
    index,
  } = useFarmV2FromSymbol(farm.lpSymbol);
  const { allowance, tokenBalance, stakedBalance, earnings } =
    useFarmV2User(index);

  const lpAddress = getAddress(lpAddresses);
  const lpName = farm.lpSymbol.toUpperCase();

  const isApproved =
    account && ((allowance && allowance.isGreaterThan(0)) || stakedBalance?.isGreaterThan(0));

  const web3 = useWeb3();

  const lpContract = getBep20Contract(lpAddress, web3);
  const farmContract = getFarmV2Contract(getAddress(farm.farmAddress), web3);

  const { onApprove } = useApprove(lpContract, farmContract);

  const handleApprove = useCallback(async () => {
    try {
      setRequestedApproval(true);
      await onApprove();
      setRequestedApproval(false);
    } catch (e) {
      console.error(e);
    }
  }, [onApprove]);
  const renderApprovalOrStakeButton = () => {
    return isApproved ? (
      <StakeAction
        onApprove={onApprove}
        allowance={allowance}
        stakedBalance={stakedBalance}
        tokenBalance={tokenBalance}
        tokenName={lpName}
        index={index}
        addLiquidityUrl={addLiquidityUrl}
        farmAddress={farm.farmAddress}
      />
    ) : (
      <Button
        mt="8px"
        width="100%"
        disabled={requestedApproval}
        onClick={handleApprove}
      >
        {TranslateString(758, "Approve Contract")}
      </Button>
    );
  };

  return (
    <Action>
      <Flex>
        <Text
          bold
          textTransform="uppercase"
          color="secondary"
          fontSize="12px"
          pr="3px"
        >
          IBIT
        </Text>
        <Text bold textTransform="uppercase" color="textSubtle" fontSize="12px">
          {TranslateString(1072, "Earned")}
        </Text>
      </Flex>
      <HarvestAction
        earnings={earnings}
        farmAddress={getAddress(farm.farmAddress)}
        index={index}
      />
      <Flex>
        <Text
          bold
          textTransform="uppercase"
          color="secondary"
          fontSize="12px"
          pr="3px"
        >
          {lpName}
        </Text>
        <Text bold textTransform="uppercase" color="textSubtle" fontSize="12px">
          {TranslateString(1074, "Staked")}
        </Text>
      </Flex>
      <Action>
        {!account ? (
          <ConnectWalletButton mt="8px" width="100%" />
        ) : (
          renderApprovalOrStakeButton()
        )}
      </Action>
    </Action>
  );
};

export default CardActions;
