import React, { useEffect, useCallback, useState, Suspense, lazy } from "react";
import { Route, useRouteMatch, useLocation } from "react-router-dom";
import { useDispatch } from "react-redux";
import BigNumber from "bignumber.js";
import { useWeb3React } from "@web3-react/core";
import { Image, RowType, Toggle, Text } from "@pancakeswap-libs/uikit";
import styled, { keyframes } from "styled-components";
import { BLOCKS_PER_YEAR, CAKE_POOL_PID } from "config";
import FlexLayout from "components/layout/Flex";
import Page from "components/layout/Page";
import Landing from "components/custom/Landing";
import HexBg from "assets/images/hexBg.png";
import IBITShape from "assets/images/IBIT_shape2.png";
import IBITShape2 from "assets/images/infibit_shape.png";
import {
  useFarmsV2,
  usePriceBnbBusd,
  usePriceX2PBusd,
  usePriceCakeBusd,
} from "state/hooks";
import useRefresh from "hooks/useRefresh";
import { fetchFarmV2UserDataAsync } from "state/actions";
import { QuoteToken } from "config/constants/types";
import { FarmV2 } from "state/types";
import useI18n from "hooks/useI18n";
import { getBalanceNumber } from "utils/formatBalance";
import { orderBy } from "lodash";
import Heading from "./Heading";
import Nebula from "./Nebula";

import FarmCard, {
  FarmV2WithStakedValue,
} from "./components/FarmCard/FarmCard";

// background: #000000 url(${HexBg});
const Wrapper = styled.div`
  padding: 0;
  max-width: 100vw;
  background: #000000 ;
  background-position: top;
  background-size: contain;
  background-repeat: no-repeat;
`;

// background: #000000 url(${HexBg});
const Container = styled.div`
  padding: 0;
  max-width: 100vw;
  z-index: 2;
  min-height: calc(100vh - 400px);
`;

const ControlContainer = styled.div`
  display: flex;
  width: 100%;
  align-items: center;
  position: relative;
  justify-content: space-between;
  flex-direction: column;

  ${({ theme }) => theme.mediaQueries.sm} {
    flex-direction: row;
    flex-wrap: wrap;
    padding: 16px 32px;
  }
`;

const Hero = styled.div`
  position: relative;
  // color: ${({ theme }) => theme.colors.textSubtle};
  color: #6BE03B;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  margin-left: auto;
  margin-right: auto;
  max-width: 85%;
  padding: 18px 0 10px 0;
  ul {
    margin: 0;
    padding: 0;
    list-style-type: none;
    font-size: 16px;
    li {
      margin-bottom: 4px;
    }
  }
  @media (min-width: 576px) {
    flex-direction: row;
    margin: 0;
    max-width: none;
    padding: 48px 0;
  }
  ul {
    margin-bottom: 2rem;
  }
`;
const ToggleWrapper = styled.div`
  display: flex;
  align-items: center;
  margin-left: 10px;

  ${Text} {
    margin-left: 8px;
  }
`;

const LabelWrapper = styled.div`
  > ${Text} {
    font-size: 12px;
  }
`;

const FilterContainer = styled.div`
  display: flex;
  align-items: center;
  width: 100%;
  padding: 8px 0px;

  ${({ theme }) => theme.mediaQueries.sm} {
    width: auto;
    padding: 0;
  }
`;

const ViewControls = styled.div`
  flex-wrap: wrap;
  justify-content: space-between;
  display: flex;
  align-items: center;
  width: 100%;

  > div {
    padding: 8px 0px;
  }

  ${({ theme }) => theme.mediaQueries.sm} {
    justify-content: flex-start;
    width: auto;

    > div {
      padding: 0;
    }
  }
`;

const FarmVersionLabel = styled.div`
  width: 100%;
  text-align: center;
  color: #fff;
  padding: 30px 0;
  text-shadow: 0 0 0.1em #6BE03B, 0 0 0.2em #6BE03B, 0 0 0.4em #6BE03B,
    0 0 0.8em #6BE03B, 0 0 1.6em #6BE03B;
`;

const leftToRight = keyframes`
  0% {
    transform: rotateX(0deg) translateX(0px);
  }
  50% {
    transform: rotateX(0deg) translateX(50px);
  }
  100% {
    transform: rotateX(0deg) translateX(0px);
  }
`;

const alltuchtopdown = keyframes`
  0% {
    transform: rotateX(0deg) translateY(0px);
  }
  50% {
    transform: rotateX(0deg) translateY(-30px);
  }
  100% {
    transform: rotateX(0deg) translateY(0px);
  }
`;

const AnimateImg = styled.img`
  top: 25%;
  right: -2%;
  animation: ${alltuchtopdown} 5s infinite linear;
  position: absolute;
  z-index: 10000000;
  max-width: 100%;
  transition: all 0.3s ease-out 0s;
  vertical-align: middle;
`;

const AnimateImgTwo = styled.img`
  top: 25%;
  left: -3%;
  animation: ${leftToRight} 5s infinite linear;
  position: absolute;
  z-index: 10000000;
  max-width: 100%;
  transition: all 0.3s ease-out 0s;
  vertical-align: middle;
`;

const Farms: React.FC = () => {
  const { path } = useRouteMatch();
  const farmsLP = useFarmsV2();
  const cakePrice = usePriceCakeBusd();
  const x2pPrice = usePriceX2PBusd();
  const bnbPrice = usePriceBnbBusd();
  const { account } = useWeb3React();

  const dispatch = useDispatch();
  const { fastRefresh } = useRefresh();
  useEffect(() => {
    if (account) {
      dispatch(fetchFarmV2UserDataAsync(account));
    }
  }, [account, dispatch, fastRefresh]);

  const activeFarms = farmsLP;

  const farmsList = useCallback(
    (farmsToDisplay): FarmV2WithStakedValue[] => {
      const cakePriceVsBNB = new BigNumber(cakePrice.div(bnbPrice));
      const farmsToDisplayWithAPY: FarmV2WithStakedValue[] = farmsToDisplay.map(
        (farm: FarmV2, index: number) => {
          if (!farm.tokenAmount || !farm.lpTotalInQuoteToken) {
            return farm;
          }

          const cakeRewardPerBlock = new BigNumber(farm.rewardTokenPerBlock);
          const cakeRewardPerYear = cakeRewardPerBlock.times(BLOCKS_PER_YEAR);

          const lpTotalInQuoteToken = new BigNumber(farm.lpTotalInQuoteToken);

          let apy = cakePriceVsBNB
            .times(cakeRewardPerYear)
            .div(lpTotalInQuoteToken);

          if (farm.quoteTokenSymbol === QuoteToken.CAKE) {
            // Regular IBIT farms
            apy = cakeRewardPerYear.div(farm.lpTokenBalanceMC);
          } else {
            // LP Farms
            apy = cakeRewardPerYear
              .times(cakePrice)
              .div(lpTotalInQuoteToken.times(bnbPrice));
          }

          let liquidity = farm.lpTotalInQuoteToken;

          if (!farm.lpTotalInQuoteToken) {
            liquidity = null;
          }
          if (farm.quoteTokenSymbol === QuoteToken.BNB) {
            liquidity = bnbPrice.times(farm.lpTotalInQuoteToken);
          }
          if (farm.quoteTokenSymbol === QuoteToken.CAKE) {
            liquidity = cakePrice.times(farm.lpTokenBalanceMC);
          }
          if (farm.quoteTokenSymbol === QuoteToken.X2P) {
            liquidity = x2pPrice.times(farm.lpTokenBalanceMC);
          }

          return { ...farm, apy, liquidity };
        },
      );

      return farmsToDisplayWithAPY;
    },
    [bnbPrice, cakePrice, x2pPrice],
  );

  const farmsStaked = farmsList(activeFarms);

  const renderContent = (): JSX.Element => {
    return (
      <div style={{ marginTop: "2rem" }}>
        <FlexLayout>
          <Route exact path={`${path}`}>
            {farmsStaked.map((farm, i) => (
              <FarmCard
                index={i}
                key={farm.lpSymbol}
                farm={farm}
                bnbPrice={bnbPrice}
                cakePrice={cakePrice}
                account={account}
                removed={false}
              />
            ))}
          </Route>
          <Route exact path={`${path}/history`}>
            {farmsStaked.map((farm, i) => (
              <FarmCard
                key={farm.lpSymbol}
                index={i}
                farm={farm}
                bnbPrice={bnbPrice}
                cakePrice={cakePrice}
                account={account}
                removed
              />
            ))}
          </Route>
        </FlexLayout>
      </div>
    );
  };

  return (
    <>
      <Wrapper>
        <Hero>
          <div>
            <Heading />
          </div>
        </Hero>
        <Container style={{ position: "relative" }}>
          <ControlContainer>
            <ViewControls>
              <ToggleWrapper />
            </ViewControls>
            <FilterContainer>
              <LabelWrapper />
            </FilterContainer>
          </ControlContainer>
          {renderContent()}
          <FarmVersionLabel>
            <a target="_blank" rel="noreferrer" href="https://t.me/IBIT_eth">
              Need help with staking tokens? <u>Visit us in Telegram</u>
            </a>
            <br />
          </FarmVersionLabel>
        </Container>
        <Landing />
      </Wrapper>
    </>
  );
};

export default Farms;
