import React from "react";
import styled from "styled-components";
import BigNumber from "bignumber.js";
import {
  Button,
  Flex,
  Heading,
  IconButton,
  AddIcon,
  MinusIcon,
  useModal,
} from "@pancakeswap-libs/uikit";
import useI18n from "hooks/useI18n";
import { useStakeV2 } from "hooks/useStake";
import { useUnstakeV2 } from "hooks/useUnstake";
import { getBalanceNumber } from "utils/formatBalance";
import { getAddress } from "utils/addressHelpers";
import { Address } from "config/constants/types";
import DepositModal from "../DepositModal";
import WithdrawModal from "../WithdrawModal";

interface FarmV2CardActionsProps {
  stakedBalance?: BigNumber;
  tokenBalance?: BigNumber;
  allowance?: BigNumber;
  tokenName?: string;
  index?: number;
  addLiquidityUrl?: string;
  farmAddress: Address;
  onApprove: () => void;
}

const IconButtonWrapper = styled.div`
  display: flex;
  svg {
    width: 20px;
  }
`;

const StakeAction: React.FC<FarmV2CardActionsProps> = ({
  stakedBalance,
  allowance,
  tokenBalance,
  tokenName,
  index,
  addLiquidityUrl,
  farmAddress,
  onApprove,
}) => {
  const TranslateString = useI18n();
  const { onStake } = useStakeV2(getAddress(farmAddress), index);
  const { onUnstake } = useUnstakeV2(getAddress(farmAddress), index);

  const rawStakedBalance = getBalanceNumber(stakedBalance, 18);
  const displayBalance = rawStakedBalance.toLocaleString();
  const [onPresentDeposit] = useModal(
    <DepositModal
      allowance={allowance}
      onApprove={onApprove}
      max={tokenBalance}
      onConfirm={onStake}
      tokenName={tokenName}
      addLiquidityUrl={addLiquidityUrl}
      decimals={18}
    />,
  );
  const [onPresentWithdraw] = useModal(
    <WithdrawModal
      max={stakedBalance}
      onConfirm={onUnstake}
      tokenName={tokenName}
      decimals={18}
    />,
  );

  const renderStakingButtons = () => {
    return rawStakedBalance === 0 ? (
      <Button onClick={onPresentDeposit}>
        {TranslateString(999, "Stake")}
      </Button>
    ) : (
      <IconButtonWrapper>
        <IconButton variant="tertiary" onClick={onPresentWithdraw} mr="6px">
          <MinusIcon color="primary" width="14px" />
        </IconButton>
        <IconButton variant="tertiary" onClick={onPresentDeposit}>
          <AddIcon color="primary" width="14px" />
        </IconButton>
      </IconButtonWrapper>
    );
  };

  return (
    <Flex justifyContent="space-between" alignItems="center">
      <Heading color={rawStakedBalance === 0 ? "textDisabled" : "text"}>
        {displayBalance}
      </Heading>
      {renderStakingButtons()}
    </Flex>
  );
};

export default StakeAction;
