import styled from 'styled-components'

const FlexLayout = styled.div`
  display: flex;
  -webkit-box-pack: center;
  justify-content: center;
  flex-wrap: wrap;
  max-width: 1200px;
  margin: 0 auto;

  & > * {
    align-self: baseline;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding: 24px;
    position: relative;
    text-align: center;
    margin: 10px;
  }
`

export default FlexLayout
